import { Link } from "gatsby";
import React from "react"
import "../../style/home/home.scss"
import Animated from "../animated"
import { throws } from "assert";

class Header extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      show: false,
      hamburger: false,
      open: false,
      rotate: null
    }
  }

  timeout;
  timeout1;

  onResize = () => {
    const isSmall = window.innerWidth <= 650;
    this.setState(state => state.hamburger === isSmall ? null : {
      hamburger: isSmall,
      show: !isSmall
    }
    )
  }

  toggleMenu = (e) => {
    this.toggleIcon();
    if (this.state.show) {
      this.setState({
        open: !this.state.open
      }, () => {
        if (this.timeout) {
          clearTimeout(this.timeout)
        }
        if (this.state.open) {
          this.timeout = setTimeout(() => {
            this.setState({
              show: false,
              open: false
            })
          }, 700);
        }
      })
    } else {
      this.setState({
        show: true
      })
    }
  }

  componentDidMount() {
    const isSmall = window.innerWidth <= 650;
    this.setState({
      hamburger: isSmall,
      show: !isSmall
    })

    window.addEventListener("resize", this.onResize)
  }


  toggleIcon = () => {
    this.setState({
      rotate: !this.state.rotate
    })
  }

  onBlur = (e) => {
    if (this.timeout1) {
      clearTimeout(this.timeout1);
    }
    if (!this.state.rotate) {
      return null
    } else {
      this.timeout1 = setTimeout(() => {
        this.toggleMenu(e)
      }, 100);
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.onResize);
    clearTimeout(this.timeout);
    clearTimeout(this.timeout1)
  }

  render() {
    const { hamburger, show, open, rotate } = this.state;
    return (
      <aside className={hamburger ? "hamburger" : ""}>
        {hamburger && <button onBlur={this.onBlur} tabIndex={1} className={rotate ? "btnClicked" : "btnUnClicked"} onClick={this.toggleMenu}><i className={rotate ? "icon-close" : "icon-menu"} /></button>}
        {
          show && <Animated
            from={hamburger ? { transform: "translateX(-100%)" } : {}}
            to={hamburger ? { transform: "translateX(0%)" } : {}}
            inverse={open}
          >
            {
              style => <nav style={hamburger ? style : {}}>
                <ul>
                  <li>
                    <Link
                      to="/"
                      activeClassName="active"
                    >
                      Home
                          </Link>
                  </li>
                  <li>
                    <Link
                      activeClassName="active"
                      to="/works/"
                    >
                      Art Works
                          </Link>
                  </li>
                  <li>
                    <Link
                      activeClassName="active"
                      to="/portfolio/"
                    >
                      Comercial Portfolios
                          </Link>
                  </li>
                  <li>
                    <Link
                      activeClassName="active"
                      to="/studio/"
                    >
                      studio
                          </Link>
                  </li>
                  <li className="social">
                    <div className="social-icons">
                      <i className="icon-facebook-logo"></i>
                      <i className="icon-instagram-logo"></i>
                      <i className="icon-black-back-closed-envelope-shape"></i>
                    </div>
                  </li>
                </ul>
              </nav>
            }
          </Animated>
        }
      </aside>
    );
  }
}

export default Header;
